html {
  color: #222; }

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none; }

::selection {
  background: #b3d4fc;
  text-shadow: none; }

html {
  box-sizing: border-box;
  font-size: 100%; }

body {
  overflow: hidden; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  background: #212121;
  font-family: "Roboto", sans-serif; }

a {
  color: #f0f0f0; }

::selection {
  background-color: #319795;
  color: #4c51bf; }

.bg-img {
  width: 100%;
  height: 100vh;
  background: #212121 url(/dist/images/bg-md.jpg) center/cover;
  opacity: 0.2; }

.title {
  position: absolute;
  bottom: 0;
  left: -2px;
  z-index: 10;
  pointer-events: none; }
  .title .primary {
    width: fit-content;
    font-family: "Anton", sans-serif;
    font-size: 4.6rem;
    letter-spacing: -1px;
    line-height: 0.8;
    text-transform: uppercase;
    color: #f0f0f0; }

@media only screen and (min-width: 1024px) {
  .bg-img {
    background: #212121 url(/dist/images/bg-lg.jpg) center/cover; } }

@media only screen and (min-width: 680px) {
  .title .primary {
    font-size: 10vw; } }

.nav {
  width: 100%;
  height: 5rem;
  position: absolute; }

.nav-list {
  width: 60%;
  justify-content: space-between; }

.nav-item {
  padding: 0.5rem;
  position: relative; }
  .nav-item .icon {
    font-size: 1.5rem; }
  .nav-item .square {
    width: 45px;
    height: 45px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #f0f0f0;
    border-radius: 5px;
    opacity: 0; }
  .nav-item:hover .square {
    opacity: 1; }

.page-active .icon {
  color: #319795; }

.page-active .square {
  opacity: 1; }

@media only screen and (min-width: 768px) {
  .nav {
    justify-content: flex-end;
    padding: 0 5vw; }
  .nav-list {
    width: 40%; } }

@media only screen and (min-width: 1024px) {
  .nav-list {
    max-width: 20%; } }

.home .title .secondary {
  width: fit-content;
  padding: 0 5px;
  font-family: "Romanesco", cursive;
  font-size: 2rem;
  margin: 0 0 0.5rem 0;
  background: linear-gradient(to right, #4c51bf, #319795);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text; }

.skills {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.03;
  pointer-events: none; }

.skills-list {
  width: 100vh;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-68%, -50%) rotate(90deg); }

.skills-item {
  color: #f0f0f0;
  font-family: "Anton", sans-serif;
  text-transform: uppercase;
  margin-bottom: 0.5rem; }

.skills-item.first {
  font-size: 4rem; }

.skills-item.second {
  font-size: 2.5rem; }

.skills-item.third {
  font-size: 2.5rem; }

.skills-item.fourth {
  font-size: 2rem; }

@media only screen and (min-width: 546px) {
  .title .secondary-box {
    display: flex;
    justify-content: flex-end; }
  .title .secondary {
    margin-bottom: 1rem; }
  .skills-list {
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    transform: translate(0%, 0%) rotate(0deg); }
  .skills-item {
    text-align: right; }
  .skills-item.first {
    font-size: 12vw; }
  .skills-item.second {
    font-size: 7vw; }
  .skills-item.third {
    font-size: 7vw; }
  .skills-item.fourth {
    font-size: 6vw; } }
